<template>
  <div class="space-y-6">
    <div class="flex justify-between items-center">
      <h1 class="text-2xl font-bold text-gray-900">Inventory</h1>
      <Button class="draped-dreams-primary">
        <FeatherIcon name="plus" class="w-4 h-4 mr-2" />
        Update Inventory
      </Button>
    </div>

    <div class="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
      <div class="bg-white rounded-lg shadow p-6">
        <div class="flex items-center justify-between mb-4">
          <h3 class="text-lg font-semibold text-gray-900">Low Stock Alert</h3>
          <Badge variant="warning">{{ lowStockItems.length }} items</Badge>
        </div>
        <div class="space-y-2">
          <div
            v-for="item in lowStockItems"
            :key="item.id"
            class="flex justify-between items-center p-2 bg-yellow-50 rounded"
          >
            <span class="text-sm font-medium">{{ item.name }}</span>
            <span class="text-sm text-yellow-600">{{ item.stock }} left</span>
          </div>
        </div>
      </div>

      <div class="bg-white rounded-lg shadow p-6">
        <div class="flex items-center justify-between mb-4">
          <h3 class="text-lg font-semibold text-gray-900">Out of Stock</h3>
          <Badge variant="danger">{{ outOfStockItems.length }} items</Badge>
        </div>
        <div class="space-y-2">
          <div
            v-for="item in outOfStockItems"
            :key="item.id"
            class="flex justify-between items-center p-2 bg-red-50 rounded"
          >
            <span class="text-sm font-medium">{{ item.name }}</span>
            <span class="text-sm text-red-600">0 left</span>
          </div>
        </div>
      </div>

      <div class="bg-white rounded-lg shadow p-6">
        <div class="flex items-center justify-between mb-4">
          <h3 class="text-lg font-semibold text-gray-900">Total Value</h3>
          <Badge variant="success">₹{{ totalInventoryValue }}</Badge>
        </div>
        <div class="text-2xl font-bold text-gray-900 mb-2">
          {{ totalItems }} items
        </div>
        <div class="text-sm text-gray-600">
          Across {{ categories.length }} categories
        </div>
      </div>
    </div>

    <!-- Inventory Table -->
    <div class="bg-white rounded-lg shadow">
      <div class="px-6 py-4 border-b border-gray-200">
        <h2 class="text-lg font-semibold text-gray-900">All Products</h2>
      </div>
      <div class="overflow-x-auto">
        <table class="min-w-full divide-y divide-gray-200">
          <thead class="bg-gray-50">
            <tr>
              <th class="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Product
              </th>
              <th class="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Category
              </th>
              <th class="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Current Stock
              </th>
              <th class="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Unit Price
              </th>
              <th class="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Total Value
              </th>
              <th class="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Status
              </th>
            </tr>
          </thead>
          <tbody class="bg-white divide-y divide-gray-200">
            <tr v-for="item in inventory" :key="item.id">
              <td class="px-6 py-4 whitespace-nowrap">
                <div class="text-sm font-medium text-gray-900">{{ item.name }}</div>
                <div class="text-sm text-gray-500">{{ item.sku }}</div>
              </td>
              <td class="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                {{ item.category }}
              </td>
              <td class="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                {{ item.stock }}
              </td>
              <td class="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                ₹{{ item.unitPrice }}
              </td>
              <td class="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                ₹{{ item.totalValue }}
              </td>
              <td class="px-6 py-4 whitespace-nowrap">
                <Badge :variant="getStockStatusVariant(item.stock)">
                  {{ getStockStatus(item.stock) }}
                </Badge>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, computed, onMounted } from 'vue'

const inventory = ref([])
const lowStockItems = ref([])
const outOfStockItems = ref([])
const categories = ref([])

const totalItems = computed(() => inventory.value.length)
const totalInventoryValue = computed(() => {
  return inventory.value.reduce((sum, item) => sum + parseInt(item.totalValue.replace(/,/g, '')), 0).toLocaleString()
})

const getStockStatus = (stock) => {
  if (stock === 0) return 'Out of Stock'
  if (stock <= 10) return 'Low Stock'
  return 'In Stock'
}

const getStockStatusVariant = (stock) => {
  if (stock === 0) return 'danger'
  if (stock <= 10) return 'warning'
  return 'success'
}

onMounted(() => {
  // Simulate loading inventory
  setTimeout(() => {
    inventory.value = [
      {
        id: 1,
        name: 'Silk Sari Collection',
        sku: 'SS-001',
        category: 'Silk',
        stock: 25,
        unitPrice: '15,000',
        totalValue: '3,75,000'
      },
      {
        id: 2,
        name: 'Cotton Sari Set',
        sku: 'CS-002',
        category: 'Cotton',
        stock: 45,
        unitPrice: '3,500',
        totalValue: '1,57,500'
      },
      {
        id: 3,
        name: 'Designer Sari',
        sku: 'DS-003',
        category: 'Designer',
        stock: 8,
        unitPrice: '25,000',
        totalValue: '2,00,000'
      },
      {
        id: 4,
        name: 'Wedding Sari',
        sku: 'WS-004',
        category: 'Wedding',
        stock: 0,
        unitPrice: '35,000',
        totalValue: '0'
      },
      {
        id: 5,
        name: 'Casual Sari',
        sku: 'CAS-005',
        category: 'Casual',
        stock: 5,
        unitPrice: '2,500',
        totalValue: '12,500'
      }
    ]

    lowStockItems.value = inventory.value.filter(item => item.stock > 0 && item.stock <= 10)
    outOfStockItems.value = inventory.value.filter(item => item.stock === 0)
    categories.value = [...new Set(inventory.value.map(item => item.category))]
  }, 1000)
})
</script>
